import React from 'react'
import styled from 'styled-components'
import tw from 'tailwind.macro'
import { Parallax } from 'react-spring/addons.cjs'

// Components
import Layout from '../components/Layout'
import ProjectCard from '../components/ProjectCard'

// Elements
import Inner from '../elements/Inner'
import { Title, BigTitle, Subtitle } from '../elements/Titles'

// Views
import Hero from '../views/Hero'
import Projects from '../views/Projects'
import About from '../views/About'
import Contact from '../views/Contact'

import avatar from '../images/avatar.jpg'

const ProjectsWrapper = styled.div`
  ${tw`flex flex-wrap justify-between mt-8`};
  display: grid;
  grid-gap: 4rem;
  grid-template-columns: repeat(2, 1fr);
  @media (max-width: 1200px) {
    grid-gap: 3rem;
  }
  @media (max-width: 900px) {
    grid-template-columns: 1fr;
    grid-gap: 2rem;
  }
`

const AboutHero = styled.div`
  ${tw`flex flex-col lg:flex-row items-center mt-8`};
`

const Avatar = styled.img`
  ${tw`rounded-full w-32 xl:w-48 shadow-lg h-auto`};
`

const AboutSub = styled.span`
  ${tw`text-white pt-12 lg:pt-0 lg:pl-12 text-2xl lg:text-3xl xl:text-4xl`};
`

const AboutDesc = styled.p`
  ${tw`text-grey-light text-lg md:text-xl lg:text-2xl font-sans pt-6 md:pt-12 text-justify`};
`

const ContactText = styled.p`
  ${tw`text-grey-light font-sans text-xl md:text-2xl lg:text-3xl`};
`

const Footer = styled.footer`
  ${tw`text-center text-grey absolute pin-b p-6 font-sans text-md lg:text-lg`};
`

const Index = () => (
  <>
    <Layout />
    <Parallax pages={5}>
      <Hero>
        <BigTitle>
          Hi, <br /> I'm Øyvind Løvskeid Taranrød,
        </BigTitle>
        <Subtitle>25 year old, living in Oslo. Studying Computer Science at The University of Oslo (UiO). </Subtitle>
      </Hero>
      <Projects>
        <Title>Find me on the interwebz</Title>
        <ProjectsWrapper>
          <ProjectCard
            title="Twitter"
            link="https://twitter.com/oyvindlt"
            bg="linear-gradient(to right, #D585FF 0%, #00FFEE 100%)"

          >
            I usually post random stuff that I find interesting. Like space rockets and awesome tech.
          </ProjectCard>
          <ProjectCard
            title="GitHub"
            link="https://github.com/eywin"
            bg="linear-gradient(to right, #662D8C 0%, #ED1E79 100%)"
          >
            You can find the source code for this site there!
          </ProjectCard>
          <ProjectCard
            title="Soundcloud"
            link="https://soundcloud.com/lucidsmash"
            bg="linear-gradient(to right, #D4145A 0%, #FBB03B 100%)"

          >
            Yup, I'm a musician too. Though it's been a while since I last made something I felt like sharing.
          </ProjectCard>
          <ProjectCard
            title="LinkedIn"
            link="https://www.linkedin.com/in/%C3%B8yvind-l%C3%B8vskeid-taranr%C3%B8d-b1b048155/"
            bg="linear-gradient(to right, #009245 0%, #FCEE21 100%)"
          >
            Everyone says its a good thing to have ;)
          </ProjectCard>

        </ProjectsWrapper>
      </Projects>
      <About>
        <Title>About</Title>
        <AboutHero>
          <Avatar src={avatar} alt="Øyvind Løvskeid Taranrød" />
          <AboutSub>
            Eywin aka Øyvind, unfortunately my name can be hard to pronounce if you weren't born with skis on your feet.
          </AboutSub>
        </AboutHero>
        <AboutDesc>
          I have a bad taste of jokes...
        </AboutDesc>
      </About>
      <Contact>
        <Inner>
          <Title>Thanks for stopping by!</Title>
          <ContactText>
            Say <a href="mailto:oyvindlt@ifi.uio.no">Hi</a> if you want :)
          </ContactText>
        </Inner>
        <Footer>
          &copy; 2018 by Gatsby Starter Portfolio Cara.{' '}
          <a href="https://github.com/LekoArts/gatsby-starter-portfolio-cara">Github Repository</a>. Made by{' '}
          <a href="https://www.lekoarts.de">LekoArts</a>.
        </Footer>
      </Contact>
    </Parallax>
  </>
)

export default Index
